
















import VideoItem from "@/components/VideoItem.vue";
import {Row, Col} from 'element-ui';
import {Component, Vue, Prop, Ref, Watch} from "vue-property-decorator";

@Component({
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    VideoItem
  },
})
export default class VideoList extends Vue {

  vheight = 0

  @Prop({default:[], type: Array}) videos?: Array<any>

  @Ref("videoItem") videoItem: any

  @Watch('videos', {immediate: true, deep: true})
  onChangeValue(newVal: Array<any>, oldVal: Array<any>){
    this.$nextTick(()=>{
      if (this.videos && this.videos.length > 0) {
        var itemWidth = this.videoItem[0].$el.clientWidth;
        var cWidth = itemWidth||0;
        var cHeight = (cWidth / 9) * 16;
        this.vheight = cHeight;
      }
    });
  }
}
