










import {Component, Prop, Vue, Ref} from "vue-property-decorator";

@Component({})
export default class VideoItem extends Vue{

  @Prop({type:String}) id? : string
  @Prop({type:String}) poster? : string
  @Prop({type:String}) name? : string
  @Prop({type:String}) episode? : string
  @Prop({default:0}) vheight?: number


  handleGoDetail() {
    this.$router.push({path:'/detail?id='+this.id+"&name="+this.name});
  }

}
