
































import {Input, Button} from 'element-ui';
import {Component, Vue} from "vue-property-decorator";

@Component({
  components: {
    [Input.name]: Input,
    [Button.name]: Button,
  }
})
export default class TopBar extends Vue {

  keyword = ""

  handleMenuClick(path:string) {
    if (this.$route.path === "/") {
      if (path !== this.$route.path) {
        this.$router.push({path:path})
      }
    } else {
      if (path.indexOf(this.$route.path) === -1) {
        this.$router.push({path:path})
      }
    }
  }

  handleSearch() {
    if (this.keyword.trim() == "") {
      this.$message({
        message: '请输入查询条件',
        type: 'warning'
      });
      this.keyword = "";
      return;
    }
    window.location.href="/#/search?page=1&keyword="+this.keyword;
  }
}
