





















import TopBar from "@/components/TopBar.vue";
import BottomContent from "@/components/BottomContent.vue";
import VideoList from "@/components/VideoList.vue";
import AxiosUtils from '@/commons/AxiosUtils';
import MovieUrls from "@/commons/NetworkUtils"
import { Component, Vue } from 'vue-property-decorator';
import CryptoJS from 'crypto-js';
import UaParserJS from 'ua-parser-js';

@Component({
  components: {
    BottomContent,
    TopBar,
    VideoList
  }
})
export default class Home extends Vue {

  homeListData: Array<any> = [];

  created() {

    const parser = new UaParserJS();
    var device = parser.getResult().device;
    if (device.type && device.type == 'mobile') {
      window.location.href = "https://5ihejin.cn/";
    }

    // var that = this;
    var key  = CryptoJS.enc.Utf8.parse('38383838363636363232323238383838');
    let ecparams = CryptoJS.AES.encrypt('{"size":12}', key, {
      mode: CryptoJS.mode.ECB,
      padding: CryptoJS.pad.Pkcs7
    });

    console.log(ecparams.ciphertext.toString())
    AxiosUtils.get(MovieUrls.movie_urls.movie_recent, {params:{"p":ecparams.ciphertext.toString()}}).then((data:any) => {

      let newData = []
      let oldData = data.data.data
      for(let i = 0;i<oldData.length;i++) {
        let current = {
          'name':'',
          'list':[]
        };
        current.name = oldData[i][0]['video_type'];
        current.list = oldData[i]
        newData.push(current)
      }

      this.homeListData = newData;
    }).catch((error:any)=>{
      console.log(error);
    });
  }

  handleGoMore(type:string) {
    console.log(type)
    if (type === '电影') {
      this.$router.push({path:'/dianying?page=1'})
    } else if (type === '电视') {
      this.$router.push({path:'/dianshi?page=1'})
    } else if (type === '综艺') {
      this.$router.push({path:'/zongyi?page=1'})
    } else if (type === '动漫') {
      this.$router.push({path:'/dongman?page=1'})
    }
  }
}
